import { ProductListComponent } from './product-list/product-list.component';
import { AddProductComponent } from './add-product/add-product.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { TableViewModule } from 'src/app/common-module/table-view/table-view.module';
import { ProductService } from 'src/app/service/product.service';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';

const routes: Routes = [
  {
    path: "",
    component: ProductListComponent,
    data: { title: 'product_list' }
  },
  {
    path: "add",
    component: AddProductComponent,
    data: { title: 'add_product' }
  },
  {
    path: 'edit/:id',
    component: AddProductComponent,
    data: { title: 'edit_product' }
  },
]

@NgModule({
  declarations: [ProductListComponent, AddProductComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    SharedModule,
    PaginationModule,
    TableViewModule,
    NgSelectModule
  ], providers: [ProductService]
})

export class ProductModule { }
